import request from '@/utils/request'
let baseUrl='http://148.100.87.15:8080'

export default {
  test() {
    return request({
      url: '/test',
      method: 'get'
    })
  },
  getAllClient(){
    return request({
      url: `${baseUrl}/client`,
      method: 'get'
    })    
  },
  getClientById(query){
    return request({
      url: `${baseUrl}/client/private?id=${query.clientID}`,
      method: 'get'
    })    
  },
  putClientById(query){
    return request({
      url: `${baseUrl}/client/modify?id=${query.clientID}&name=${query.name}&birthdate=${query.birthdate}&address=${query.address}&occupation=${query.occupation}`,
      method: 'put'
    })    
  },
  getAccountAll(){
    return request({
      url: `${baseUrl}/account`,
      method: 'get'
    })    
  },
  getAccountById(query){
    return request({
      url: `${baseUrl}/account/private?id=${query.clientID}`,
      method: 'get'
    })    
  },
  postAccountDeposit(query){
    return request({
      url: `${baseUrl}/account/deposit?id=${query.accountID}&balance=${query.balance}`,
      method: 'post'
    })    
  },
  postAccountWithdraw(query){
    return request({
      url: `${baseUrl}/account/withdraw?id=${query.accountID}&balance=${query.balance}`,
      method: 'post'
    })    
  },
  getRecordAll(){
    return request({
      url: `${baseUrl}/record`,
      method: 'get'
    })  
  },
  postRecord(query){
    return request({
      url: `${baseUrl}/record?accountID=${query.accountID}&receiver=${query.receiver}&amount=${query.amount}`,
      method: 'post'
    })  
  },

}